export default {
	props: {
		item: Object,
		index: Number
	},
	methods: {
		guanzhu(){
			this.item.isFollow=true;
			uni.showToast({
				title:'关注成功'
			})
		},
		caozuo(type) {
			switch (type) {
				case "ding":
					if (this.item.infoNum.index == 1) {
						return;
					}
					this.item.infoNum.agreeNum++;
					if (this.item.infoNum.index == 2) {
						this.item.infoNum.disAgreeNum--;
					}
					this.item.infoNum.index = 1;

					break;
				case "cai":
					if (this.item.infoNum.index == 2) {
						return;
					}
					this.item.infoNum.disAgreeNum++;
					if (this.item.infoNum.index == 1) {
						this.item.infoNum.agreeNum--;
					}
					this.item.infoNum.index = 2;
					break;
			}
		}
	}

}
